import axios from 'axios';

let base = 'api';


export const requestLogin = params => { return axios.post(`${base}/login`, params).then(res => res.data); };

export const getUserList = params => { return axios.get(`${base}/user/list`, { params: params }); };

export const getDsListPage = params => { return axios.get(`${base}/datasource/list`, { params: params }); };

export const delDsList = params => { return axios.delete(`${base}/datasource/list`, { params: params }); };

export const removeUser = params => { return axios.get(`${base}/user/remove`, { params: params }); };

export const batchRemoveUser = params => { return axios.get(`${base}/user/batchremove`, { params: params }); };

export const editUser = params => { return axios.get(`${base}/user/edit`, { params: params }); };

export const addDatasource = params => { return axios.post(`${base}/datasource`, params); };

export const putDatasource = params => { return axios.put(`${base}/datasource`, params); };

export const delDatasource = params => { return axios.delete(`${base}/datasource`, { params: params }); };

export const getDsTables = params => { return axios.get(`${base}/datasource/tables`, { params: params }); };

export const getPreviewData = params => { return axios.get(`${base}/data/joinpreview`, { params: params }); };

export const getAggrData = params => { return axios.get(`${base}/data/aggrpreview`, { params: params }); };

export const getTableSchema = params => { return axios.get(`${base}/table/schema`, { params: params }); };

export const addTable = params => { return axios.post(`${base}/table`, params); };

export const getChart = params => { return axios.get(`${base}/chart`, { params: params }); };

export const addChart = params => { return axios.post(`${base}/chart`, params); };

export const delChart = params => { return axios.delete(`${base}/chart`, { params: params }); };

export const putChart = params => { return axios.put(`${base}/chart`, params); };

export const delDashboard = params => { return axios.delete(`${base}/dashboard`, { params: params }); };

export const getDashboardList = params => { return axios.get(`${base}/dashboard/list`); };

export const getDashboardCharts = params => { return axios.get(`${base}/dashboard/chartlist`, { params: params }); };

export const getDashboardChart = params => { return axios.get(`${base}/dashboard/chart`, { params: params }); };

export const addDashboards = params => { return axios.post(`${base}/dashboard/list`, params); };

export const delFile = params => { return axios.delete(`${base}/file`, { params: params }); };


