<template>
  <div class="chart-container">
    <div align="right" style="margin:20px 0px;">
      <el-button icon="share" size='small' @click="share()"/>
      <el-button icon="delete" size='small' @click="rmdash()"/>
    </div>
    <el-row>
      <el-col v-for="name in charts" :key="name" v-loading="!finished[name]" :span="24">
        <div align="right">
          <el-button icon="edit" size='mini' @click="edit(name)"/>
          <el-button icon="close" size='mini' @click="remove(name)"/>
        </div>
        <div :id="'chart-'+name" style="width:100%; height:400px;" @mounted="drawChart(name)"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import echarts from 'echarts'
import { getDashboardCharts, getDashboardChart, delChart, delDashboard } from '../../api/api'
import chart from '../../common/chart'
  
export default {
  data() {
    return {
      finished: {},
      chart: {},
      charts: [],
      name: null
    }
  },
  methods: {
    share() {
      alert('share '+this.name);
    },
    rmdash() {
      this.$confirm('确认删除吗？', '提示', {}).then(() => {
        delDashboard({"name": this.name}).then((res) => {
          this.$message({message: "删除成功", type: 'info'});
          this.$router.push({ path: '/dashboard' });
        }).catch(error => {
          console.log(JSON.stringify(error), error);
          this.handleError(error, "删除失败")
        });
      }).catch(error => {});
    },
    handleError(error, message) {
      if(error.hasOwnProperty('response') && error.response.status == 401){
        this.$message({message: '登陆失效',  type: 'error'});
        this.$router.push({ path: '/login' });
      } else {
        var msg=message;
        if(!error.hasOwnProperty('response')) msg=message;
        else if(!error.response.hasOwnProperty('data')) msg=message;
        else if(!error.response.data.hasOwnProperty('message')) msg=message;
        else msg=message+':'+error.response.data.message;
        this.$message({message: msg, type: 'error'});
      };
    },
    getChartList() {
      getDashboardCharts({"dashboard": this.name}).then((res) => {
        this.charts = res.data.charts;
        this.$nextTick(function () {
          for(var i in this.charts) {
            this.drawChart(this.charts[i]);
          }
        })
      }).catch(error => {
        console.log(JSON.stringify(error), error);
        this.handleError(error, "仪表盘 "+this.name+" 图表列表获取失败")
        this.$router.push({ path: '/404' });
      });
    },
    drawChart(name) {
      if(this.chart.hasOwnProperty(name)) {
        return;
      }
      getDashboardChart({"dashboard": this.$route.params.name, "chart": name}).then((res) => {
        var data = res.data;
        var option = chart.draw_options(name, data.type, data.columns, data.data);
        
        var doc = document.getElementById('chart-'+name);
        if(!doc) return;
        this.chart[name] = echarts.init(document.getElementById('chart-'+name));
        this.chart[name].setOption(option);
        
        this.$set(this.finished, name, true);
      }).catch(error => {
        this.$set(this.finished, name, true);
        console.log(JSON.stringify(error), error);
        this.handleError(error, "图表 "+name+" 加载失败")
      });
    },
    edit(name){
      this.$router.push({ path: '/chart-maker', query: {bashboard: this.name, chart: name} });
    },
    remove(name){
      this.$confirm('确认删除吗？', '提示', {}).then(() => {
        delChart({"dashboard": this.name, 'chart': name}).then((res) => {
          var index = this.charts.indexOf(name);
          if(index!=-1) this.charts.splice(index, 1);
          this.$message({message: "删除成功", type: 'info'});
          this.$router.push({ path: '/dashboard/'+this.name });
        }).catch(error => {
          console.log(JSON.stringify(error), error);
          this.handleError(error, "删除失败")
        });
      })
    }
  },
  mounted() {
    this.name = this.$route.params.name;
    this.getChartList();
  },
  updated() {
  },
  watch: {
  '$route' (to, from) {
    this.name = this.$route.params.name;
    for(var key in this.chart) {
      this.chart[key].dispose();
    }
    this.chart = {};
    this.charts = [];
    this.finished = {};
    this.getChartList();
  }
}
};
</script>

<style scoped>
.chart-container {
  width: 100%;
  float: left;
}
.el-col {
  padding: 30px 20px;
}
</style>


