<template>
  <div>
    <el-form label="图表制作"  v-loading='eloading' label-width="100px" style="margin:20px 0px;" label-position="left">
      <el-form-item label="数据源">
        <el-select v-model="form.ds" filterable placeholder="请选择数据源" @change="getTables">
          <el-option v-for="item in datasources" :label="item.name" :value="item.id" :key="item.id">
            <span style="float: left">{{ item.name }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.args }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="数据表">
        <el-select v-model="form.table" placeholder="请选择" @change='getSchema'>
          <el-option-group v-for="(group, index) in groups" :key="group" :label="group">
            <el-option v-for="item in tables[index]" :key="item" :label="item" :value="item"/>
          </el-option-group>
        </el-select>
      </el-form-item>
      <el-form-item label="维度">
        <el-select v-model="form.xFields" multiple placeholder="请选择字段">
          <el-option v-for="item in fields" :label="item" :value="item" :key="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="数值">
        <div v-for="field in form.yFields" class="block">
          <el-cascader expand-trigger="hover" :options="field_options" v-model="field.name"/>
          <el-button icon="plus" @click.prevent="addValue(field)"></el-button>
          <el-button icon="delete" @click.prevent="removeValue(field)"></el-button>
        </div>
      </el-form-item>
      <el-form-item label="图表类型">
        <el-radio-group v-model="form.type" @change="draw()">
<!--          <el-radio-button label="table" :disabled="!showChart()">表 格</el-radio-button>-->
          <el-radio-button label="bar" :disabled="disabledbar()">柱形图</el-radio-button>
          <el-radio-button label="line" :disabled="disabledline()">折线图</el-radio-button>
          <el-radio-button label="stack" :disabled="disabledstack()">堆积图</el-radio-button>
          <el-radio-button label="area" :disabled="disabledarea()">面积图</el-radio-button>
          <el-radio-button label="stackarea" :disabled="disabledstackarea()">面积堆积图</el-radio-button>
          <el-radio-button label="funnel" :disabled="disabledfunnel()">漏斗图</el-radio-button>
          <el-radio-button label="radar" :disabled="disabledradar()">雷达图</el-radio-button>
          <el-radio-button label="pie" :disabled="disabledpie()">饼 图</el-radio-button>
        </el-radio-group>
      </el-form-item>
      <el-form-item>
        <el-button @click="previewChart">预览图表</el-button>
        <el-button type="primary" @click="onSure" v-if="!modify">立即创建</el-button>
        <el-button type="primary" @click="onSure" v-else>立即保存</el-button>
      </el-form-item>
    </el-form>
    <el-dialog title="创建图表" :visible.sync="ensure">
      <el-form :model="form">
        <el-form-item label="图表名称" label-width='100px'>
          <el-input :disabled="modify" v-model="form.chart_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="仪表盘名称" label-width='100px'>
          <el-select :disabled="modify" v-model="form.dashboards" multiple filterable allow-create placeholder="请选择仪表盘，输入新名称可创建">
            <el-option v-for="item in dashboards" :key="item.name" :label="item.name" :value="item.name" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="ensure = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
    <div v-show="showChart()" id="chart" v-loading='cloading' style="width:100%; height:400px;"></div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import { getDsListPage, getDashboardChart, getDsTables, getChart, putChart, 
          getTableSchema, getDashboardList, addChart, addDashboards } from '../../api/api'
  import chart from '../../common/chart'
  
  export default {
      data() {
          return {
            form: {
              table: '',
              ds: null,
              xFields: [],
              yFields: [{name: []}],
              type: 'bar',
              chart_name: '',
              dashboards: []
            },
            modify: false,
            cloading: false,
            groups: ["合表", "基础表"],
            datasources: [],
            tables: [[], []],
            fields: [],
            chart: null,
            xAxis: [],
            yAxis: {},
            ensure: false,
            dashboards: [],
            field_options: [],
            drawed: false,
            eloading: false,
            aggrs_type: [{value: 'count', label: '计数'}, {value: 'distinct', label: '去重计数'},
                         {value: 'sum', label: '总和'}, {value: 'avg', label: '平均值'}]
          }
      },
      methods: {
        showChart() {
          if(this.form.ds == null) return false;
          if(this.form.table == '') return false;
          
          var yValue = false;
          for(var i in this.form.yFields) {
            if(this.form.yFields[i].name.length>0) return true;
          }
          if(this.form.xFields.length == 0) return false;
          return true;
        },
        onSure() {
          getDashboardList().then((res) => {
            this.dashboards = res.data.dashboards;
          }).catch(error => {
            this.handleError(error, "获取仪表盘列表失败");
          });
          this.ensure = true;
        },
        onSubmit() {
          if(this.form.chart_name == '') {
            this.$message({message: '请填写图表名称', type: 'warning'});
            return;
          }
          if(this.form.dashboards.length == 0 ) {
            this.$message({message: '请至少选择或创建一个仪表盘', type: 'warning'});
            return;
          }
          if(this.modify){
            var data = {
              table: this.form.table, ds_id: this.form.ds, type: this.form.type,
              xFields: this.form.xFields, yFields: this.form.yFields, 
              name: this.form.chart_name, dashboard: this.form.dashboards[0]
            };
            putChart(data).then((res) => {
              this.$message({message: '保存成功', type: 'info'});
              this.$router.push({ path: '/dashboard/'+this.form.dashboards[0] });
            }).catch(error => {
              this.handleError(error, "保存失败");
            });
          }
          else {
            addDashboards({names: this.form.dashboards}).then((res) => {
              console.log('dashboard list post successful!');
            }).catch(error => {
              this.handleError(error, "保存失败");
              return;
            });

            var data = {
              table: this.form.table, ds_id: this.form.ds, type: this.form.type,
              xFields: this.form.xFields, yFields: this.form.yFields, 
              name: this.form.chart_name, dashboards: this.form.dashboards
            };
            addChart(data).then((res) => {
              this.$message({message: '保存成功', type: 'info'});
              this.$router.push({ path: '/dashboard/'+this.form.dashboards[0] });
            }).catch(error => {
              this.handleError(error, "保存失败");
            });
          }
        },
        removeValue(item) {
          if(this.form.yFields.length <= 1) {
            item.name = [];
            return;
          }
          var index = this.form.yFields.indexOf(item)
          if (index !== -1) {
            this.form.yFields.splice(index, 1)
          }
        },
        addValue(item) {
          var index = this.form.yFields.indexOf(item);
          this.form.yFields.splice(index+1, 0, {name: [], key: Date.now()});
        },
        getDadasources() {
          getDsListPage({}).then((res) => {
            this.datasources = res.data.datasources.map(
              function(x){
                return {'id': x.id, 'name': x.name, 'args': 
                        x.filelist?
                        (x.filelist.toString().length>21?x.filelist.toString().substring(0, 18)+'...':x.filelist.toString()):
                        (x.host+':'+x.port)};
              }
            )
          }).catch(error => {
            this.handleError(error, "数据源列表获取失败")
          })
        },
        getTables(ds) {
          this.tables = [[], []];
          this.fields = [];
          getDsTables({"ds_id": ds}).then((res) => {
            if(res.data.vtables.indexOf(this.form.table)==-1 &&
               res.data.tables.indexOf(this.form.table)==-1)
              this.form.table = '';
            this.tables = [
              res.data.vtables, res.data.tables
            ];
          }).catch(error => {
            this.handleError(error, "数据表列表获取失败");
          })
        },
        getSchema(tb) {
          getTableSchema({"ds_id": this.form.ds, "tables": JSON.stringify([tb])}).then((res) => {
              this.fields = res.data.schema[0].schema;
              this.field_options = [];
              for(var i in this.fields){
                var field = this.fields[i];
                this.field_options.push({value: field, label: field, children: this.aggrs_type});
              }
            }
          ).catch(error => {
            this.handleError(error, "字段信息获取失败")
          });
        },
        handleError(error, message) {
          console.log(error);
          if(error.hasOwnProperty('response') && error.response.status == 401){
            this.$message({message: '登陆失效',  type: 'error'});
            this.$router.push({ path: '/login' });
          } else {
            var msg=message;
            if(!error.hasOwnProperty('response')) msg=message;
            else if(!error.response.hasOwnProperty('data')) msg=message;
            else if(!error.response.data.hasOwnProperty('message')) msg=message;
            else msg=message+':'+error.response.data.message;
            this.$message({message: msg, type: 'error'});
          };
        },
        draw() {
          if(this.chart == null) return;
          this.chart.clear();
          
          if(this['disabled'+this.form.type]()) this.form.type='bar';
          var option = chart.draw_options('预览图表', this.form.type, this.xAxis, this.yAxis);
          this.chart.setOption(option);
        },
        disabledradar() {
          return this.drawed && Object.keys(this.yAxis).length == 0;
        },
        disabledfunnel() {
          return this.drawed && Object.keys(this.yAxis).length > 1;
        },
        disabledstack(){
          return this.drawed && Object.keys(this.yAxis).length < 2;
        },
        disabledstackarea() {
          return this.drawed && Object.keys(this.yAxis).length < 2;
        },
        disabledarea() {
          return this.drawed && Object.keys(this.yAxis).length == 0;
        },
        disabledpie() {
          return this.drawed && Object.keys(this.yAxis).length > 1;
        },
        disabledbar() {
          return this.drawed && Object.keys(this.yAxis).length == 0;
        },
        disabledline() {
          return this.drawed && Object.keys(this.yAxis).length == 0;
        },
        previewChart() {
          if(!this.showChart()) return;
          var data = {
            table: this.form.table, ds_id: this.form.ds, type: this.form.type,
            xFields: JSON.stringify(this.form.xFields),
            yFields: JSON.stringify(this.form.yFields)
          };
          this.cloading = true;
          getChart(data).then((res) => {
            this.xAxis = res.data.columns;
            this.yAxis = res.data.data;
            if(this.chart == null) this.chart = echarts.init(document.getElementById('chart'));
            this.draw();
            this.drawed = true;
            this.cloading = false;
          }).catch(error => {
            // this.chart.clear();
            this.cloading = false;
            document.getElementById('chart').text = '图表数据异常';
            console.log(JSON.stringify(error));
            this.handleError(error, "图表数据异常")
          });
        }
      },
      mounted() {
        if(Object.keys(this.$route.query).length!=0) {
          this.eloading = true;
          this.modify = true;
          var query = this.$route.query;
          getDashboardChart({"dashboard": query.bashboard, "chart": query.chart}).then((res) => {
            this.form.ds = res.data.ds_id;
            this.form.table = res.data.table;
            this.form.xFields = res.data.xFields;
            this.form.yFields = res.data.yFields.map(function (x) { return {name: [x.name, x.aggr_func]}});
            this.form.type = res.data.type;  
            this.eloading = false;
            this.form.chart_name = query.chart;
            this.form.dashboards = [query.bashboard];
            this.previewChart();
          }).catch(error => {
            console.log(JSON.stringify(error), error);
            this.handleError(error, "图表 "+query+" 加载失败")
          });
        }
        this.getDadasources();
      }
  }
</script>



