import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import Aggr from './views/nav1/Aggr.vue'
import Chart from './views/nav1/Chart.vue'
import echarts from './views/charts/echarts.vue'
import dashboard from './views/nav3/dashboard.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-database',
        leaf: true,
        children: [
            { path: '/datasource', component: Table, name: '数据源' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '图表配置',
        iconCls: 'fa fa-table',
        children: [
            { path: '/aggr', component: Aggr, name: '聚合筛选' },
            { path: '/multi-table', component: Form, name: '关联合表' },
            { path: '/chart-maker', component: Chart, name: '制作图表' }
        ]
    },
    {
        path: '/dashboard',
        component: Home,
        name: '仪表盘',
        iconCls: 'fa fa-area-chart',
        autolist: 'dashboardlist',
        children: [
           { path: '/dashboard/:name', component: dashboard, name: '仪表盘-' }
        ]
    },
//    {
//        path: '/',
//        component: Home,
//        name: '仪表盘测试',
//        iconCls: 'fa fa-bar-chart',
//        children: [
//            { path: '/echarts', component: echarts, name: 'echarts' }
//        ]
//    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


