<template>
  <div>
    <el-form label="聚合筛选" label-width="100px" style="margin:20px 0px;" label-position="left">
      <el-form-item label="数据源">
        <el-select v-model="form.ds" filterable placeholder="请选择数据源" @change="getTables">
          <el-option v-for="item in datasources" :label="item.name" :value="item.id" :key="item.id">
            <span style="float: left">{{ item.name }}</span>
            <span style="float: right; color: #8492a6; font-size: 13px">{{ item.args }}</span>
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="数据表">
        <el-select v-model="form.table" placeholder="请选择" @change='getSchema'>
          <el-option-group v-for="(group, index) in groups" :key="group" :label="group">
            <el-option v-for="item in tables[index]" :key="item" :label="item" :value="item"/>
          </el-option-group>
        </el-select>
      </el-form-item>
      <el-form-item label="过滤条件">
        <div v-for="field in form.filters" class="block">
          <el-row>
          <el-col  :span="6">
          <el-select v-model="field.name" placeholder="字段">
            <el-option v-for="item in fields" :label="item" :value="item" :key="item"/>
          </el-select>
          </el-col>
          <el-col  :span="6">
          <el-select v-model="field.operator" placeholder="运算符">
            <el-option v-for="item in operators" :label="item.label" :value="item.value" :key="item.key"/>
          </el-select>
          </el-col>
          <el-col  :span="6">
          <el-input v-model="field.value" auto-complete="off">
            <el-select v-model="field.value_type" slot="prepend" placeholder="请选择">
              <el-option label="数  值" value="value" key='1'/>
              <el-option label="表达式" value="func" key='2'/>
            </el-select>
          </el-input>
          </el-col>
          <el-button icon="plus" @click.prevent="addFilter(field)"></el-button>
          <el-button icon="delete" @click.prevent="removeFilter(field)"></el-button>
          </el-row>
        </div>
      </el-form-item>
      <el-form-item label="维度">
        <el-select v-model="form.xFields" multiple placeholder="请选择字段">
          <el-option v-for="item in fields" :label="item" :value="item" :key="item"/>
        </el-select>
      </el-form-item>
      <el-form-item label="数值">
        <div v-for="field in form.yFields" class="block">
          <el-cascader expand-trigger="hover" :options="field_options" v-model="field.name"/>
          <el-button icon="plus" @click.prevent="addValue(field)"></el-button>
          <el-button icon="delete" @click.prevent="removeValue(field)"></el-button>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button @click="previewData">预览数据</el-button>
        <el-button type="primary" @click="onSure" v-if="!modify">立即创建</el-button>
        <el-button type="primary" @click="onSure" v-else>立即保存</el-button>
      </el-form-item>
    </el-form>
    <el-dialog title="创建合表" :visible.sync="ensure">
      <el-form :model="form">
        <el-form-item label="合表名称" label-width='100px'>
          <el-input :disabled="modify" v-model="form.name" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="ensure = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
    <div v-if="table != null" id="chart" v-loading='loading' style="width:100%; height:400px;">
      <el-tag type="success" style="margin:5px 0px">预览</el-tag>
      <el-table :data="table.data" stripe style="margin:5px 0px;">
        <el-table-column v-for="column in table.columns" :key="column" :prop="column" :label="column">
          <template scope="scope">
            <el-popover placement="top" trigger="hover" :content="scope.row[column]">
              <div slot="reference" class="name-wrapper">
                <el-tag color='transparent'><font color="black">{{ scope.row[column] }}</font></el-tag>
              </div>
            </el-popover>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  import { getDsListPage, getDsTables, getTableSchema, getAggrData, addTable } from '../../api/api'
  import chart from '../../common/chart'
  
  export default {
      data() {
          return {
            form: {
              table: '',
              ds: null,
              xFields: [],
              yFields: [{name: []}],
              filters: [{name: null, operator: null, value: null, value_type: 'value'}],
              name: ''
            },
            modify: false,
            table: null,
            loading: false,
            groups: ["合表", "基础表"],
            datasources: [],
            fields: [],
            tables: [[], []],
            operators: [{label: "等于", value: '='}, {label: "不等于", value: '!='},
                        {label: "大于", value: '>'}, {label: "不小于", value: '>='},
                        {label: "小于", value: '<'}, {label: "不大于", value: '<='},
                        {label: "包含", value: 'like'}, {label: "不包含", value: 'not like'},
                        {label: "为空", value: 'null'}, {label: "不为空", value: 'not null'},],
            ensure: false,
            field_options: [],
            aggrs_type: [{value: 'count', label: '计数'}, {value: 'distinct', label: '去重计数'},
                         {value: 'sum', label: '总和'}, {value: 'avg', label: '平均值'}]
          }
      },
      methods: {
        showData() {
          if(this.form.ds == null) return false;
          if(this.form.table == '') return false;
          return true;
        },
        onSure() {
          if(this.table == null) {
            this.$message({message: '信息不完善', type: 'warning'});
            return;
          }
          this.ensure = true;
        },
        onSubmit() {
          if(this.form.name == '') {
            this.$message({message: '信息不完善', type: 'warning'});
            return;
          }
          var rules = {table: this.form.table, x_fields: this.form.xFields, 
                       y_fields: this.form.yFields, filters: this.form.filters};
          addTable({"ds_id": this.form.ds, "rules": rules, "name": this.form.name, type: 'aggr'}).then((res) => {
            this.$message({message: '保存成功',  type: 'info'});
          }).catch(error => {
            console.log(JSON.stringify(error));
            this.handleError(error, "保存失败")
          });
          this.ensure = false;
        },
        removeValue(item) {
          if(this.form.yFields.length <= 1) {
            item.name = [];
            return;
          }
          var index = this.form.yFields.indexOf(item)
          if (index !== -1) {
            this.form.yFields.splice(index, 1)
          }
        },
        addValue(item) {
          var index = this.form.yFields.indexOf(item);
          this.form.yFields.splice(index+1, 0, {name: [], key: Date.now()});
        },
        removeFilter(item) {
          if(this.form.filters.length <= 1) {
            this.form.filters = [{name: null, operator: null, value: null, value_type: 'value'}];
            return;
          }
          var index = this.form.filters.indexOf(item)
          if (index !== -1) {
            this.form.filters.splice(index, 1)
          }
        },
        addFilter(item) {
          var index = this.form.filters.indexOf(item);
          this.form.filters.splice(index+1, 0, {name: '', operator: '', value: '', value_type: 'value', key: Date.now()});
        },
        getDadasources() {
          getDsListPage({}).then((res) => {
            this.datasources = res.data.datasources.map(
              function(x){
                return {'id': x.id, 'name': x.name, 'args': 
                        x.filelist?
                        (x.filelist.toString().length>21?x.filelist.toString().substring(0, 18)+'...':x.filelist.toString()):
                        (x.host+':'+x.port)};
              }
            )
          }).catch(error => {
            this.handleError(error, "数据源列表获取失败")
          })
        },
        getTables(ds) {
          this.tables = [[], []];
          this.fields = [];
          getDsTables({"ds_id": ds}).then((res) => {
            if(res.data.vtables.indexOf(this.form.table)==-1 &&
               res.data.tables.indexOf(this.form.table)==-1)
              this.form.table = '';
            this.tables = [
              res.data.vtables, res.data.tables
            ];
          }).catch(error => {
            this.handleError(error, "数据表列表获取失败");
          })
        },
        getSchema(tb) {
          getTableSchema({"ds_id": this.form.ds, "tables": JSON.stringify([tb])}).then((res) => {
              this.fields = res.data.schema[0].schema;
              this.field_options = [];
              for(var i in this.fields){
                var field = this.fields[i];
                this.field_options.push({value: field, label: field, children: this.aggrs_type});
              }
            }
          ).catch(error => {
            this.handleError(error, "字段信息获取失败")
          });
        },
        handleError(error, message) {
          console.log(error);
          if(error.hasOwnProperty('response') && error.response.status == 401){
            this.$message({message: '登陆失效',  type: 'error'});
            this.$router.push({ path: '/login' });
          } else {
            var msg=message;
            if(!error.hasOwnProperty('response')) msg=message;
            else if(!error.response.hasOwnProperty('data')) msg=message;
            else if(!error.response.data.hasOwnProperty('message')) msg=message;
            else msg=message+':'+error.response.data.message;
            this.$message({message: msg, type: 'error'});
          };
        },
        previewData() {
          if(!this.showData()) return;
          var filters = [];
          var data = {
            table: this.form.table, ds_id: this.form.ds,
            xFields: JSON.stringify(this.form.xFields),
            yFields: JSON.stringify(this.form.yFields),
            filters: JSON.stringify(this.form.filters)
          };
          this.loading = true;
          getAggrData(data).then((res) => {
            this.table = res.data;
            for(var i in this.table.data) {
              for(var key in this.table.data[i]) {
                this.table.data[i][key] = String(this.table.data[i][key]);
              }
            }
            this.loading = false;
          }).catch(error => {
            this.handleError(error, '数据加载失败');
            this.loading = false;
          });
        }
      },
      mounted() {
        this.getDadasources();
      }
  }
</script>

<style>
  .el-input .el-select {
    width: 90px;
  }
</style>



