import Mock from 'mockjs';
const LoginUsers = [
  {
    id: 1,
    username: 'admin',
    password: '123456',
    avatar: 'https://raw.githubusercontent.com/taylorchen709/markdown-images/master/vueadmin/user.png',
    name: '张某某'
  }
];

const Users = [];

for (let i = 0; i < 86; i++) {
  Users.push(Mock.mock({
    id: Mock.Random.guid(),
    name: Mock.Random.cname(),
    ctime: Mock.Random.datetime(), // Mock.mock('@county(true)'),
    // 'args|18-60': 1,
    host: '192.168.1.1',
    port: '3306',
    file: 'test.csv',
    utime: Mock.Random.datetime(),
    type: ['MYSQL', 'MSSQL', 'ORACLE', 'CSV'][Mock.Random.integer(0, 3)],
    username: 'root',
    password: '123456',
    filelist: []
  }));
}

export { LoginUsers, Users };



