export default {
  draw_options(title, type, xAxis, yAxis) {
    if(this.hasOwnProperty(type)) {
      return Object.assign(
        {title: {text: title}}, this[type](xAxis, yAxis)
      ); 
    }
    else return {};
  },
  basedraw(xAxis, yAxis, type, stack, areaStyle) {
    var series = [], legend = [];
    for(var key in yAxis){
      legend.push(key);
      series.push({name: key, data: yAxis[key], type: type, stack: stack, areaStyle: areaStyle});
    }
    return {
      xAxis: {type: 'category', data: xAxis}, tooltip: {trigger: 'axis', axisPointer: {type: 'shadow'}},
      yAxis: {type: 'value'}, series: series, legend: {data: legend}
    }
  },
  singledraw(xAxis, yAxis, type, label) {
    var series={type: type, data: [], label: label}, legend=[], key=Object.keys(yAxis)[0];
    for(var i in xAxis){
      legend.push(xAxis[i]);
      series.data.push({name: xAxis[i], value: yAxis[key][i]});
    }
    return {
      tooltip: {formatter: "{b} : {c} ({d}%)"}, series: series,
      legend: {orient: 'horizontal', left: 'center', data: legend}
    }
  },
  bar(xAxis, yAxis) {
    return this.basedraw(xAxis, yAxis, 'bar');
  },
  stack(xAxis, yAxis) {
    return this.basedraw(xAxis, yAxis, 'bar', 'stack');
  },
  line(xAxis, yAxis) {
    return this.basedraw(xAxis, yAxis, 'line');
  },
  area(xAxis, yAxis) {
    return this.basedraw(xAxis, yAxis, 'line', null, {normal: {}});
  },
  stackarea(xAxis, yAxis) {
    return this.basedraw(xAxis, yAxis, 'line', 'stack', {normal: {}});
  },
  radar(xAxis, yAxis) {
    var series = [{type: 'radar', data: []}], radar={indicator: []}, legend={data: []};
    for(var i in xAxis){
      var values = Object.values(yAxis).map(function(x){return x[i]});
      var max = Math.max.apply(null, values);
      radar.indicator.push({name: xAxis[i].toString(), max: max*1.25});
    }
    for(var key in yAxis) {
      console.log(key, JSON.stringify(yAxis[key]));
      legend.data.push(key);
      series[0].data.push({name: key, value: yAxis[key]});
    }
    return {series: series, radar: radar, legend: legend};
  },
  funnel(xAxis, yAxis) {
    return this.singledraw(xAxis, yAxis, 'funnel', {normal: {position: 'inside'}});
  },
  pie(xAxis, yAxis) {
    return this.singledraw(xAxis, yAxis, 'pie', {normal: {position: 'outside'}});
  }
};


