<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="姓名"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getDadasources">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="datasources" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" label="ID" width="60">
			</el-table-column>
			<el-table-column prop="name" label="名称" width="100" sortable>
			</el-table-column>
			<el-table-column prop="type" label="类型" width="100" sortable>
			</el-table-column>
			<el-table-column prop="args" label="参数" width="150" :formatter="formatArgs">
			</el-table-column>
			<el-table-column prop="ctime" label="创建时间" min-width="180" sortable>
			</el-table-column>
			<el-table-column prop="utime" label="修改时间" min-width="180" sortable>
			</el-table-column>
			<el-table-column label="操作" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-form-item label="数据源名称" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="数据源类型" prop="maintype">
					<el-radio-group v-model="editForm.ttype" prop="maintype" @change="typeChange">
						<el-radio class="radio" :label="0">数据库</el-radio>
						<el-radio class="radio" :label="1">文件</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="数据源类型" prop="secdtype">
				  <el-select v-model="editForm.options_value[editForm.ttype]" placeholder="请选择">
                    <el-option
                      v-for="item in options[editForm.ttype]"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
				</el-form-item>
				<div v-if="editForm.ttype === 1">
                  <el-form-item label="上传文件" prop="putfilelist">
                    <el-upload class="upload-demo" ref="putupload" :file-list="editForm.filelist" :on-success="putSuccess" :on-error="addFailed" action="/api/file" :auto-upload="false" :on-remove="putRemove" :data="{path: 'datasource/'+editForm.name}">
                      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                      <div slot="tip" class="el-upload__tip">文件大小不超过500MB</div>
                    </el-upload>
                  </el-form-item>
                </div>
                <div v-else-if="editForm.options_value[editForm.ttype] === 'OTHERDB'">
                  <el-form-item label="连接字符串" prop="connect_str">
					<el-input v-model="editForm.connect_str" auto-complete="off"></el-input>
				  </el-form-item>
                </div>
                <div v-else>
                  <el-form-item label="服务器地址" prop="host">
                    <el-input v-model="editForm.host" auto-complete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="端口" prop="port">
                    <el-input v-model="editForm.port" auto-complete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="用户名" prop="username">
				    <el-input v-model="editForm.username" auto-complete="off"></el-input>
				  </el-form-item>
                  <el-form-item label="密码" prop="password">
				    <el-input v-model="editForm.password" type="password" auto-complete="off"></el-input>
				  </el-form-item>
                </div>
                <div v-if="editForm.options_value[editForm.ttype] === 'ORACLE'">
                  <el-form-item label="连接类型">
                    <el-radio-group v-model="editForm.oracle_type">
                      <el-radio class="radio" label="SID">SID</el-radio>
                      <el-radio class="radio" label="SERVERNAME">服务名</el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item v-bind:label="editForm.oracle_type==='SID'?'SID':'服务名'">
					<el-input v-model="editForm.oracle_value" auto-complete="off"></el-input>
				  </el-form-item>
                </div>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">
				<el-form-item label="数据源名称" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"/>
				</el-form-item>
				<el-form-item label="数据源类型" prop="maintype">
					<el-radio-group v-model="addForm.ttype" prop="type" @change="typeChange">
						<el-radio class="radio" :label="0">数据库</el-radio>
						<el-radio class="radio" :label="1">文件</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="数据源类型" prop="secdtype">
				  <el-select v-model="addForm.options_value[addForm.ttype]" placeholder="请选择">
                    <el-option
                      v-for="item in options[addForm.ttype]"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
				</el-form-item>
				<div v-if="addForm.ttype === 1">
                  <el-form-item label="上传文件" prop="addfilelist">
                    <el-upload class="upload-demo" ref="addupload" :on-success="addSuccess" :on-error="addFailed" :on-remove="addRemove" :file-list="addForm.filelist" action="/api/file" :auto-upload="false" :data="{path: 'datasource/'+addForm.name}">
                      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                      <div slot="tip" class="el-upload__tip">文件大小不超过50MB</div>
                    </el-upload>
                  </el-form-item>
                </div>
                <div v-else-if="addForm.options_value[addForm.ttype] === 'OTHERDB'">
                  <el-form-item label="连接字符串" prop="connect_str">
					<el-input v-model="addForm.connect_str" auto-complete="off"></el-input>
				  </el-form-item>
                </div>
                <div v-else>
                  <el-form-item label="服务器地址" prop="host">
                    <el-input v-model="addForm.host" auto-complete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="端口" prop="port">
                    <el-input v-model="addForm.port" auto-complete="off"></el-input>
                  </el-form-item>
                  <el-form-item label="用户名" prop="username">
				    <el-input v-model="addForm.username" auto-complete="off"></el-input>
				  </el-form-item>
                  <el-form-item label="密码" prop="password">
				    <el-input v-model="addForm.password" type="password" auto-complete="off"></el-input>
				  </el-form-item>
                </div>
                <div v-if="addForm.options_value[addForm.ttype] === 'ORACLE'">
                  <el-form-item label="连接类型">
                    <el-radio-group v-model="addForm.oracle_type">
                      <el-radio class="radio" label="SID">SID</el-radio>
                      <el-radio class="radio" label="SERVERNAME">服务名</el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <el-form-item v-bind:label="addForm.oracle_type==='SID'?'SID':'服务名'">
					<el-input v-model="addForm.oracle_value" auto-complete="off"></el-input>
				  </el-form-item>
                </div>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	import { getDsListPage, delDatasource, batchRemoveUser, putDatasource, addDatasource, delDsList, delFile } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				datasources: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				//编辑界面数据
				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
//					name: [{ required: true, message: '请输入数据源名称', trigger: 'blur' }],
//                    maintype: [{ required: true, message: '请选择数据源类型', trigger: 'blur' }],
//                    secdtype: [{ required: true, message: '请选择数据源类型', trigger: 'blur' }],
//                    connect_str: [{ required: true, message: '请输入数据源连接串', trigger: 'blur' }],
//                    filelist: [{ required: true, message: '请选择文件', trigger: 'blur' }],
//                    host: [{ required: true, message: '请输入服务地址', trigger: 'blur' }],
//                    port: [{ required: true, message: '请输入端口地址', trigger: 'blur' }]
				},
				editForm: {
					id: 0,
					name: '',
                    ttype: 0,
                    type: 'MYSQL',
                    host: '',
                    port: '',
                    username: '',
                    password: '',
                    oracle_type: 'SID',
                    oracle_value: '',
                    connect_str: '',
                    filelist: [],
                    options_value: ['MYSQL', 'CSV']
				},
                options: [
                    [{
                        value: 'MYSQL',
                        label: 'MYSQL'
                      }, {
                        value: 'MSSQL',
                        label: 'MSSQL'
                      }, {
                        value: 'ORACLE',
                        label: 'ORACLE'
                      }, {
                        value: 'OTHERDB',
                        label: '其他数据库'
                    }],
                    [{
                        value: 'CSV',
                        label: 'CSV'
                      }, {
                        value: 'EXCEL',
                        label: 'EXCEL'
                    }]
                ],
                
				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
//					name: [{ required: true, message: '请输入数据源名称', trigger: 'blur' }],
//                    maintype: [{ required: true, message: '请选择数据源类型', trigger: 'blur' }],
//                    secdtype: [{ required: true, message: '请选择数据源类型', trigger: 'blur' }],
//                    connect_str: [{ required: true, message: '请输入数据源连接串', trigger: 'blur' }],
//                    filelist: [{ required: true, message: '请选择文件', trigger: 'blur' }],
//                    host: [{ required: true, message: '请输入服务地址', trigger: 'blur' }],
//                    port: [{ required: true, message: '请输入端口地址', trigger: 'blur' }]
				},
                
				//新增界面数据
				addForm: {
					id: 0,
					name: '',
                    ttype: 0,
                    type: 'MYSQL',
                    host: '',
                    port: '',
                    username: '',
                    password: '',
                    oracle_type: 'SID',
                    oracle_value: '',
                    connect_str: '',
                    filelist: [],
                    options_value: ['MYSQL', 'CSV']
				}
			}
		},
		methods: {
            formatArgs: function (row, column) {
              var ttype = (this.options[0].map(function (x) {return x.value}).indexOf(row.type)==-1?1:0);
              return ttype == 0? row.host+':'+row.port : 
              (row.filelist.toString().length>21?row.filelist.toString().substring(0, 18)+'...':row.filelist.toString());
            },
            typeChange: function (value) {
              // this.type.value = '';
            },
            submitUpload() {
              this.$refs.upload.submit();
            },
            addRemove(file, fileList) {
              console.log(file, fileList);
            },
            putRemove(file, fileList) {
              this.editForm.filelist.splice(this.editForm.filelist.indexOf(file.name), 1);
            },
            handlePreview(file) {
              console.log(file);
            },
			handleCurrentChange(val) {
              this.page = val;
              this.getDadasources();
			},
            handleError(error, message) {
              this.listLoading = false;
              if(error.response.status == 401){
                this.$message({message: '登陆失效', type: 'error'});
                this.$router.push({ path: '/login' });
              } else {
                this.$message({message: message, type: 'error'})
              };
            },
			//获取用户列表
			getDadasources() {
              let para = {
                pagesize: 20,
                page: this.page,
                name: this.filters.name
              };
              this.listLoading = true;
              getDsListPage(para).then((res) => {
                this.total = res.data.total;
                this.datasources = res.data.datasources;
                this.listLoading = false;
              }).catch(error => {
                this.handleError(error, "数据源列表获取失败")
              })
			},
			//删除
			handleDel: function (index, row) {
              this.$confirm('确认删除该记录吗?', '提示', {type: 'warning'}).then(() => {
                this.listLoading = true;
                let para = { id: row.id };
                delDatasource(para).then((res) => {
                  this.listLoading = false;
                  this.$message({message: '删除成功', type: 'success'});
                  this.getDadasources();
                }).catch(error => {
                  this.handleError(error, "数据源删除失败")
                });
              }).catch(() => {});
			},
			//显示编辑界面
			handleEdit: function (index, row) {
              this.editFormVisible = true;
              var ttype = (this.options[0].map(function (x) {return x.value}).indexOf(row.type)==-1?1:0);
              var options_value = ['MYSQL', 'CSV'];
              var filelist = row.filelist && row.filelist.map(function(x){return {name: x}});
              options_value[ttype] = row.type;
              this.editForm = Object.assign({}, {
                ttype: ttype, host: '', port: '', options_value: options_value,
                connect_str: '', oracle_type: 'SID', oracle_value: ''
              }, row, {filelist: filelist});
			},
			//显示新增界面
			handleAdd: function () {
              this.addFormVisible = true;
              var ttype = 0;
              var options_value = ['MYSQL', 'CSV'];
              this.editForm = Object.assign({}, {
                ttype: ttype, host: '', port: '',
                options_value: options_value,
                filelist: [], connect_str: '',
                oracle_type: 'SID', oracle_value: ''
              });
			},
			//编辑
			editSubmit: function () {
              this.$refs.editForm.validate((valid) => {
                if (valid) {
                  this.$confirm('确认提交吗？', '提示', {}).then(() => {
                    if(this.editForm.ttype==1 && 
                       this.$refs.putupload.uploadFiles.length>this.$refs.putupload.fileList.length) this.$refs.putupload.submit();
                    else this.putSuccess(null, null, this.editForm.filelist);
                  });
                }
              });
			},
			//新增
			addSubmit: function () {
              this.$refs.addForm.validate((valid) => {
                if (valid) {
                  this.$confirm('确认提交吗？', '提示', {}).then(() => {
                    if(this.addForm.ttype==1 && 
                       this.$refs.addupload.uploadFiles.length>this.$refs.addupload.fileList.length) this.$refs.addupload.submit();
                    else this.addSuccess(null, null, this.addForm.filelist);
                  });
                }
              });
			},
            putSuccess: function(response, file, fileList) {
              this.editLoading = true;
              var params = {host: this.editForm.host, port: this.editForm.port, 
                            filelist: fileList && fileList.map(function(x){return x.name}),
                            username: this.editForm.username, password: this.editForm.password, 
                            oracle_type: this.editForm.oracle_type, oracle_value: this.editForm.oracle_value};
              var type = this.editForm.options_value[this.editForm.ttype];
              var para = {'id': this.editForm.id, 'name': this.editForm.name, 'type': type, 'params': params};
              putDatasource(para).then((res) => {
                this.editLoading = false;
                this.$message({message: '提交成功', type: 'success'});
                this.$refs['editForm'].resetFields();
                this.editFormVisible = false;
                this.getDadasources();
              }).catch(error => {
                this.editLoading = false;
                this.handleError(error, "数据源信息修改失败")
              });
            },
            addSuccess: function(response, file, fileList) {
              this.addLoading = true;
              var params = {host: this.addForm.host, port: this.addForm.port, 
                            filelist: fileList && fileList.map(function(x){return x.name}),
                            username: this.addForm.username, password: this.addForm.password, 
                            oracle_type: this.addForm.oracle_type, oracle_value: this.addForm.oracle_value};
              var type = this.addForm.options_value[this.addForm.ttype];
              var para = {'name': this.addForm.name, 'type': type, 'params': params};
              addDatasource(para).then((res) => {
                this.addLoading = false;
                this.$message({
                    message: '提交成功',
                    type: 'success'
                });
                this.addForm.filelist = [];
                this.$refs['addForm'].resetFields();
                this.addFormVisible = false;
                this.getDadasources();
              }).catch(error => {
                this.addLoading = false;
                this.handleError(error, "数据源添加失败")
              });
            },
            addFailed: function(err, file, fileList) {
              this.$message({message: '文件上传失败：'+err.message, type: 'error'});
            },
			selsChange: function (sels) {
              this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
              var ids = this.sels.map(item => item.id);
              this.$confirm('确认删除选中记录吗？', '提示', {type: 'warning'}).then(() => {
                this.listLoading = true;
                delDsList({ ids: JSON.stringify(ids) }).then((res) => {
                  this.listLoading = false;
                  this.$message({message: '删除成功', type: 'success'});
                  this.getDadasources();
                }).catch(error => {
                  this.handleError(error, "数据源批量删除失败")
                });
              }).catch(() => {});
			}
		},
		mounted() {
			this.getDadasources();
		}
	}

</script>

<style scoped>

</style>


