<template>
    <div>
      <el-form label="数据源" label-width="100px" style="margin:20px 0px;" label-position="left">
        <el-form-item label="数据源">
          <el-select v-model="form.ds" filterable placeholder="请选择数据源" @change="getTables">
            <el-option v-for="item in form.ds_options" :label="item.name" :value="item.id" :key="item.id">
              <span style="float: left">{{ item.name }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.args }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="数据表">
          <el-select v-model="form.tb" multiple placeholder="请选择数据表" @change="getPreview">
            <el-option-group v-for="(group, index) in groups" :key="group" :label="group">
              <el-option v-for="item in form.tb_options[index]" :key="item" :label="item" :value="item"/>
            </el-option-group>
          </el-select>
        </el-form-item>
        <el-form-item label="表关联规则">
          <div v-for="(rule, index) in form.rules" :label="'关联规则' + index" :key="rule.key">
            <el-cascader expand-trigger="hover" :options="form.rule_options" v-model="rule.field1" @change="getPreview2(rule, 1)"/>
            <el-cascader expand-trigger="hover" :options="form.rule_options" v-model="rule.field2" @change="getPreview2(rule, 2)"/>
            <el-button icon="plus" @click.prevent="addRule(rule)"></el-button>
            <el-button icon="delete" @click.prevent="removeRule(rule)"></el-button>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSure">立即创建</el-button>
        </el-form-item>
      </el-form>
      
      <el-dialog title="创建合表" :visible.sync="ensure">
        <el-form :model="form">
          <el-form-item label="合表名称" label-width='100px'>
            <el-input v-model="form.tb_name" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="ensure = false">取 消</el-button>
          <el-button type="primary" @click="onSubmit">确 定</el-button>
        </div>
      </el-dialog>

      <div v-for="table in tables">
        <el-tag type="primary" v-for="name in table.names" style="margin:5px 0px" :key="name">{{ name }}</el-tag>-
        <el-tag type="success" style="margin:5px 0px">预览</el-tag>
        <el-table :data="table.data" stripe style="margin:5px 0px;">
          <el-table-column v-for="column in table.columns" :key="column" :prop="column" :label="column">
            <template scope="scope">
              <el-popover placement="top" trigger="hover" :content="scope.row[column]">
                <div slot="reference" class="name-wrapper">
                  <el-tag color='transparent'><font color="black">{{ scope.row[column] }}</font></el-tag>
                </div>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
</template>

<script>
  import { getDsListPage, getDsTables, getPreviewData, getTableSchema, addTable } from '../../api/api';
  export default {
      data() {
          return {
              form: {
                ds_options: [],
                tb_options: [[], []],
                rule_options: [],
                ds: null,
                tb: [],
                rules: [{field1: [], field2: []}],
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: '',
                tb_name: ''
              },
              groups: ['合表', '普通表'],
              ensure: false,
              tables: [],
              tableData: [['1', '2', '3', '4', '5', '6']]
          }
      },
      methods: {
        onSure() {
          if(this.form.ds == null || this.form.tb.length == 0 || 
             this.form.rules[0].field1.length == 0 ||
             this.form.rules[0].field2.length == 0) {
            this.$message({message: '信息不完善', type: 'warning'});
            return;
          }
          this.ensure = true;
        },
        onSubmit() {
          addTable({"ds_id": this.form.ds, "rules": this.form.rules, "name": this.form.tb_name}).then((res) => {
            this.$message({message: '保存成功',  type: 'info'});
          }).catch(error => {
            console.log(JSON.stringify(error));
            this.handleError(error, "保存失败")
          });
          this.ensure = false;
        },
        getHeaderWidth(column) {
          var width = (column.length*25)+'px';
          return '100%';
        },
        renderHeader(createElement, { column }) {
          return createElement(
            'div', [createElement('font', [column.label], {attrs: {color: 'red'}})], {attrs: {width: '100px'}}
          );
        },
        handleError(error, message) {
          if(error.response.status == 401){
            this.$message({
              message: '登陆失效', 
              type: 'error'
            });
            this.$router.push({ path: '/login' });
          } else {
            this.$message({
              message: message, 
              type: 'error'
            })
          };
        },
        getDadasources() {
          getDsListPage({}).then((res) => {
            this.form.ds_options = res.data.datasources.map(
              function(x){
                return {'id': x.id, 'name': x.name, 'args': 
                        x.filelist?
                        (x.filelist.toString().length>21?x.filelist.toString().substring(0, 18)+'...':x.filelist.toString()):
                        (x.host+':'+x.port)};
              }
            )
          }).catch(error => {
            this.handleError(error, "数据源列表获取失败")
          })
        },
        getTables(ds) {
          this.form.tb = [];
          getDsTables({"ds_id": ds}).then((res) => {
            this.form.tb_options = [res.data.vtables, res.data.tables]
          }).catch(error => {
            console.log(JSON.stringify(error));
            this.handleError(error, "数据表列表获取失败")
          })
        },
        getPreviewdata() {
          if(!this.form.tb.length) return;
          getTableSchema({"ds_id": this.form.ds, "tables": JSON.stringify(this.form.tb)}).then((res) => {
            var tables = res.data.schema, options=[];
            this.form.rule_options = [];
            for(var i in tables){
              var res = [], table = tables[i];
              for(var j in table.schema) {
                res.push({'value': table.schema[j], 'label': table.schema[j]});
              }
              options.push({'value': table.name, 'label': table.name, 'children': res});
            }
            for(var i in options){
              this.form.rule_options.push(options[i]);
            }
          }).catch(error => {
            console.log(JSON.stringify(error));
            this.handleError(error, "字段信息获取失败")
          });
          
          var params = {"ds_id": this.form.ds, "tables": JSON.stringify(this.form.tb), "rules": JSON.stringify(this.form.rules)};
          getPreviewData(params).then((res) => {
            this.tables = res.data.tables;
            for(var i in this.tables){
              for(var j in this.tables[i].data) {
                for(var key in this.tables[i].data[j]) {
                  this.tables[i].data[j][key] = String(this.tables[i].data[j][key]);
                }
              }
            }
          }).catch(error => {
            console.log(JSON.stringify(error));
            this.handleError(error, "预览数据获取失败")
          });
        },
        getPreview(tb) {
          this.tables = [];
          for(var i in this.form.rules){
            var rule = this.form.rules[i];
            if(rule.field1.length>0 && tb.indexOf(rule.field1[0]) == -1) {this.removeRule(rule);continue;}
            if(rule.field2.length>0 && tb.indexOf(rule.field2[0]) == -1) {this.removeRule(rule);continue;}
          }
          this.getPreviewdata();
        },
        getPreview2(item, which) {
          var index = this.form.rules.indexOf(item);
          if(this.form.rules[index].field1.length == 0) return;
          if(this.form.rules[index].field2.length == 0) return;
          if(this.form.rules[index].field1[0] == this.form.rules[index].field2[0]) {
            if(which==1) this.form.rules[index].field1 = [];
            if(which==2) this.form.rules[index].field2 = [];
            this.$message({message: '暂不支持自关联', type: 'error'})
            return;
          }
          this.tables = [];
          this.getPreviewdata();
        },
        removeRule(item) {
          if(this.form.rules.length <= 1) {
            item.field1 = item.field2 = [];
            return;
          }
          var index = this.form.rules.indexOf(item)
          if (index !== -1) {
            this.form.rules.splice(index, 1)
          }
          if(item.field1.length>0 && item.field2.length>0) this.getPreviewdata();
        },
        addRule(item) {
          var index = this.form.rules.indexOf(item);
          this.form.rules.splice(index+1, 0, {field1: [], field2:[], key: Date.now()});
        }
      },
      mounted() {
          this.getDadasources();
      }
  }
</script>



